@charset "UTF-8";
body {
  padding: 0;
  margin: 0;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, “Segoe UI”, “Roboto”, “Oxygen”, “Ubuntu”, “Cantarell”, “Fira Sans”, “Droid Sans”, “Helvetica Neue”, sans-serif; }

a {
  color: inherit;
  text-decoration: inherit; }

button {
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
  outline: inherit; }
